.navBotMenu {
    width: 100%;
    margin-top: 120px !important;
}
.navBotMenu ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow-x: scroll;
    white-space: nowrap;
    scrollbar-color: #d9d9d9;
    scrollbar-width: thin;
    scroll-behavior: smooth;
}
.navBotMenu ul::-webkit-scrollbar {
    height: 5px;
}

.navBotMenu ul::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.navBotMenu ul::-webkit-scrollbar-thumb {
    background: #d9d9d9;
    border-radius: 10px;
}


.navBotMenu ul li {
    font-family: "SF Pro Text";
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #1c1c1c;
    background: #d9d9d9;
    border: 1px solid #999999;
    border-radius: 15px;
    padding: 8px 16px;
    margin: 0px 20px;
    margin-bottom: 12px;
    cursor: pointer;
}

.navBotMenu ul li:first-child {
    margin-left: 14px;
}

.navBotMenu ul li:last-child {
    margin-right: 14px;
}

.pt-0 .courses {
    padding-top: 0 !important;
}

.courses {
    padding: 100px 24px 24px 24px;
}
.courses .course-card:last-child .course-item{
    margin-bottom: 0 !important;
}

.courses .pt-20 {
    padding-top: 20px !important;
}

.courses .container .rowGrid .p-10 {
    padding: 10px 5px !important;
}

.courses .col-6 {
    overflow-x: visible !important;
}

.ml-240 {
    margin-left: 240px !important;
    transition: all 0.2s linear;
}

.ml-100 {
    margin-left: 100px !important;
    transition: all 0.2s linear;

}

.itemello {
    position: relative !important;
}

.pop-up {
    position: absolute !important;
    left: 115%;
    bottom: 0;
    width: 370px;
    height: 100%;
    background-color: black;
    z-index: 2;
}

.pop-up .wrapper {
    position: relative !important;
    width: 100%;
    height: 100%;
}

.pop-up .wrapper .indicator {
    position: absolute !important;
    top: 250px;
    left: -20px;
    background-color: black;
    z-index: 5;
    width: 40px;
    height: 40px;
    transform: rotate(45deg);
}

.headerText {
    font-family: 'SF Pro Text';
    font-style: normal;
    font-weight: 900;
    font-size: 28px;
    line-height: 33px;
    color: #1C1C1C;
    margin: 50px 0 30px 0;

}

.activeCategory {
    background-color: #80B5FF !important;
    border: 1px solid #006AFF !important;
    color: white !important;
}

@media (max-width:576px) {
    .homepage .courses {
        /* padding: 50px 10px 24px 10px !important; */
        margin-top: 135px;
        padding-bottom: 0 !important;
    }
    /* .courses {
        padding: 150px 10px 24px 10px !important;
    } */

    .ml-240 {
        margin-left: 0px !important;
    }

    .ml-100 {
        margin-left: 0px !important;
        padding: 0px;
    }
    .navBotMenu {
        width: 100%;
        margin-top: 0 !important;
        position: fixed;
        top: 85px !important;
        right: 0;
        left: 0;
        z-index: 20;
        background-color: white;
    }
    .navBotMenu ul {
        padding: 8px 0;
        border-top: 1px solid #D9D9D9;

    }

    .navBotMenu ul li {
        margin: 0px 8px;
    }

}